    verReportes(item) {
      this.pk_inventario = item.pK_INVENTARIOT;
      this.correlativo = item.codigo;
      this.tienda = item.pK_TIENDA;
      this.semana = item.semana;
      this.fechai = item.fechainicio;
      this.fechaf = item.fechafin;
      this.verNuevo = 2;
    },
    verActa(item) {
      this.actaModal = 1;
      this.fechareporte = new Date().toLocaleString(),
      this.listarActa(item.pK_INVENTARIOT);
      this.totalImportado(item.pK_INVENTARIOT,item.semana);
      this.totalLectura(item.pK_INVENTARIOT,item.semana);
      this.totalDiferencia(item.pK_INVENTARIOT,item.semana);
    },
    exportar() {
      this.wait = 1;
      var newfecha1 = this.fecha1.split("-").reverse().join("-");
      var newfecha2 = this.fecha2.split("-").reverse().join("-");
      let me = this;
      axios({
        url: "api/InventariosT/ExportarExcel", 
        method: "POST",
        headers: {
            Authorization: "Bearer " + me.$store.state.token 
        },
        responseType: "blob",
        data: {
          fechainicio: newfecha1,
          fechafin: newfecha2
        },
      })
        .then(function (response) {
          me.cerrar();
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "Inventarios Diferencias "+ newfecha1 +"_"+ newfecha2 +".xlsx");
          document.body.appendChild(link);
          link.click();
        })
        .catch(function (error) {});
    },
    verExportar() {
      this.excelModal = true;
    },
    verGenerar() {
      switch (this.reporte){
        case "Stock Final":
        this.verStock();
        break;
        case "Planilla Inicial":
        this.verInicial();
        break;
        case "Planilla Diferencias":
        this.verDiferencia();
        break;
        case "Planilla Recapitulado":
        this.verRecapitulado();
        break;
        case "Planilla Lecturas":
        this.verLectura();
        break;
        case "Planilla Final":
        this.verFinal();
        break;
      }
    },
    listar() {
      let me = this;
      let header = { Authorization: "Bearer " + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get(
          "api/InventariosT/Catalogar/", configuracion
        )
        .then(function (response) {
          me.inventarios = response.data;
        })
        .catch(function (error) {
          if (error.response.status == 401) {
            me.$store.dispatch("salir");
          } else {
            console.log(error);
          }
        });
    },
    listarTiendas() {
      let me = this;
      let header = { Authorization: "Bearer " + this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get(
          "api/InventariosT/CatalogarTiendas/"+ parseInt(me.$store.state.usuario.pk_usuario), configuracion
        )
        .then(function (response) {
          me.inventarios = response.data;
