    },
    obtenerActa(data = []){
      for (var i = 0; i < data.length; i++) {
          var d1 = new Date(data[0].fechainicio);
          d1.setMinutes(d1.getMinutes() + d1.getTimezoneOffset())
          var d2 = new Date(data[0].fechafin);
          d2.setMinutes(d2.getMinutes() + d2.getTimezoneOffset())
          this.ciudadActa = data[0].ciudad,
          this.fechaInicioActa = d1.getDate() + " de " + this.meses[d1.getMonth()] + " de " + d1.getFullYear(),
          this.fechaFinalActa = d2.getDate() + " de " + this.meses[d2.getMonth()] + " de " + d2.getFullYear(),
          this.horaInicioActa = data[0].horainicio,
          this.horaFinalActa = data[0].horafin,
          this.tiendaActa = data[0].pK_TIENDA,
          this.direccionActa = data[0].direccion,
          this.consignatarioActa = data[0].consignatario,
          this.carnetActa = data[0].carnet,
          this.razonActa = data[0].razonsocial,
          this.nitActa = data[0].nit,
          this.auditor1Acta = data[0].nombres+" "+data[0].apellidos,
          this.ci1Acta = data[0].ci
      }
      for (var i = 1; i < data.length; i++) {
        this.auditor2Acta = data[1].nombres+" "+data[1].apellidos,
        this.ci2Acta = data[1].ci
      }
      for (var i = 2; i < data.length; i++) {
        this.auditor3Acta = data[2].nombres+" "+data[2].apellidos,
        this.ci3Acta = data[2].ci
      }
    },
    verLectura(){
      this.search = "";
      this.verLista = 3;
      this.listarLectura(this.pk_inventario,this.semana);
      this.sumaPares(this.pk_inventario, this.semana);
      this.sumaAccesorios(this.pk_inventario, this.semana);
    },
    verRecapitulado(){
      this.search = "";
      this.verLista = 2;
      this.listarRecapitulado(this.pk_inventario,this.semana);
      this.sumaPares(this.pk_inventario, this.semana);
      this.sumaAccesorios(this.pk_inventario, this.semana);
    },
    verDiferencia(){
      this.search = "";
      this.verLista = 1;
      this.listarDiferencia(this.pk_inventario,this.semana);
    },
    verStock() {
      this.tiendareporte = this.tienda,
      this.fechareporte = new Date().toLocaleString(),
      this.codigoreporte = this.correlativo,
      this.semanareporte = this.semana,
      this.listarStock(this.pk_inventario, this.semana);
      this.stockModal = 1;
    },
    verInicial(){
      this.search = "";
      this.verLista = 4;
      this.listarInicial(this.pk_inventario);
      this.sumoPares(this.pk_inventario, this.semana);
      this.sumoAccesorios(this.pk_inventario, this.semana);
    },
    verFinal(){
      this.search = "";
      this.verLista = 5;
      this.listarFinal(this.pk_inventario,);
      this.sumaPares(this.pk_inventario, this.semana);
      this.sumaAccesorios(this.pk_inventario, this.semana);
    },
    mostrarPares(data = []){
      for (var i = 0; i < data.length; i++) {
          this.cantidadPares = data[i].cantidad,
          this.importePares = data[i].total
      }
    },
    mostrarAccesorios(data = []){
      for (var i = 0; i < data.length; i++) {
          this.cantidadAccesorios = data[i].cantidad,
          this.importeAccesorios = data[i].total
      }
    },
    mostrarImportado(data = []){
      for (var i = 0; i < data.length; i++) {
          this.totalImportadoArticulos = this.formatoMiles(data[i].accesorios + data[i].pares),
          this.totalImportadoPrecio = this.formatoMiles(data[i].precioa + data[i].preciop),
          this.totalImportadoAccesorios = this.formatoMiles(data[i].accesorios),
          this.totalImportadoPares = this.formatoMiles(data[i].pares),
          this.totalImportadoPrecioA = this.formatoMiles(data[i].precioa),
          this.totalImportadoPrecioP = this.formatoMiles(data[i].preciop)
      }
    },
    mostrarLectura(data = []){
      for (var i = 0; i < data.length; i++) {
          this.totalLecturaArticulos = this.formatoMiles(data[i].accesorios + data[i].pares),
          this.totalLecturaPrecio = this.formatoMiles(data[i].precioa + data[i].preciop),
          this.totalLecturaAccesorios = this.formatoMiles(data[i].accesorios),
          this.totalLecturaPares = this.formatoMiles(data[i].pares),
          this.totalLecturaPrecioA = this.formatoMiles(data[i].precioa),
          this.totalLecturaPrecioP = this.formatoMiles(data[i].preciop)
      }
    },
    mostrarDiferencia(data = []){
      var total = 0;
      for (var i = 0; i < data.length; i++) {
          this.diferenciaTotalArticulos = this.formatoMiles(data[i].accesorios + data[i].pares),
          total = data[i].precioa + data[i].preciop,
          this.diferenciaPrecioTotal  = total.toFixed(2),
          this.difereciaTotalAccesorios = this.formatoMiles(data[i].accesorios),
          this.diferenciaTotalPares = this.formatoMiles(data[i].pares),
          this.diferenciaPrecioAccesorios = this.formatoMiles(data[i].precioa),
          this.diferenciaPrecioPares = this.formatoMiles(data[i].preciop) 
      }
    },
    obtenerPares(data = []){
      for (var i = 0; i < data.length; i++) {
          this.numeroPares = data[i].cantidad,
          this.costoPares = data[i].total
      }
    },
    obtenerAccesorios(data = []){
      for (var i = 0; i < data.length; i++) {
          this.numeroAccesorios = data[i].cantidad,
          this.costoAccesorios = data[i].total
      }
    },
    ocultarLectura() {
      this.verLista = 0;
      this.search = "";
      this.lecturas = [];
      this.cantidadPares = 0;
      this.cantidadAccesorios = 0;
      this.importePares = 0;
      this.importeAccesorios = 0;
    },
    ocultarRecapitulado() {
      this.verLista = 0;
      this.search = "";
      this.recapitulado = [];
      this.cantidadPares = 0;
      this.cantidadAccesorios = 0;
      this.importePares = 0;
      this.importeAccesorios = 0;
    },
    ocultarDiferencia() {
      this.verLista = 0;
      this.search = "";
      this.diferencias = [];
      this.totalDiferenciasPares = 0;
      this.totalDiferenciasAccesorios = 0;
      this.totalAccesorios = 0;
      this.totalPares = 0;
    },
    ocultarStock() {
      this.stockModal = 0;
      this.fechareporte = "";
      this.tiendareporte = "";
      this.codigoreporte = "";
      this.semanareporte = "";
      this.stocks = [];
    },
    ocultarDetalles() {
      this.verNuevo = 0;
      this.pk_inventario = "";
      this.semana = "";
      this.correlativo = "";
      this.tienda = "";
      this.fechai = "";
      this.fechaf = "";
      this.exportados = [];
    },
    ocultarInicial() {
      this.verLista = 0;
      this.search = "";
      this.iniciales = [];
      this.cantidadPares = 0;
      this.cantidadAccesorios = 0;
      this.importePares = 0;
      this.importeAccesorios = 0;
    },
    ocultarFinal() {
      this.verLista = 0;
      this.search = "";
      this.finales = [];
      this.cantidadPares = 0;
      this.cantidadAccesorios = 0;
      this.importePares = 0;
      this.importeAccesorios = 0;
    },
    ocultarReportes() {
      this.verNuevo = 0;
      this.correlativo = "";
      this.semana = "";
      this.tienda = "";
      this.reporte = "";
      this.fechai = "";
      this.fechaf = "";
      this.ocultarDiferencia();
      this.ocultarRecapitulado();
      this.ocultarLectura();
    },
    ocultarActa() {
      this.actaModal = 0;
      this.ciudadActa = "";
      this.fechaInicioActa = "";
      this.fechaFinalActa = "";
      this.horaInicioActa = "";
      this.horaFinalActa = "";
      this.tiendaActa = "";
      this.direccionActa = "";
      this.consignatarioActa = "";
      this.carnetActa = "";
      this.razonActa = "";
      this.nitActa = "";
      this.auditor1Acta = "";
      this.auditor2Acta = "";
      this.auditor3Acta = "";
      this.ci1Acta = "";
      this.ci2Acta = "";
      this.ci3Acta = "";
    },
    formatoMiles (number) {
      var valor = 0.0;
      const exp = /(\d)(?=(\d{3})+(?!\d))/g;
      const rep = '$1,';
      let arr = number.toString().split('.');
      arr[0] = arr[0].replace(exp, rep);
      valor = arr[1] ? arr.join('.') : arr[0];
      return valor;
    },
    cerrar() {
      this.excelModal = false;
      this.fecha1 = "";
      this.fecha2 = "";
      this.validaMensaje1 = "";
      this.validaMensaje2 = "";
      this.wait = 0;
    },
  },
};
</script>
